<template>
  <section class="detail">
    <el-form label-width="180px">

      <el-form-item label="제목" @keyup.native="onKeyup">
        <el-input v-model="form.subj"></el-input>
      </el-form-item>

      <el-form-item label="파업구분">
          <el-select v-model="form.tp">
            <el-option
              v-for="item in tpOptions"
              :key="item.value"
              :label="item.label"
              :value="item.value">
            </el-option>
          </el-select>
      </el-form-item>

      <el-form-item label="대체텍스트" v-if="form.tp === '이용동의'">
        <el-input v-model="form.conts"></el-input>
      </el-form-item>

      <el-form-item label="바로가기">
        <el-radio-group v-model="form.cnnt_url_yn">
          <el-radio label="사용"></el-radio>
          <el-radio label="미사용"></el-radio>
        </el-radio-group>
        <el-input v-model="form.cnnt_url" placeholder="팝업 이미지 클릭시 이동할 URL을 입력하세요.">
        </el-input>
      </el-form-item>

      <el-form-item label="이미지등록">
        <el-upload :disabled="form.tp === '20'"
          class="upload-demo"
          action="" 
          name="file"
          :multiple="false"
          :on-remove="onImgRemove"
          :on-change="onImgChange"
          :auto-upload="false"
          :limit="1"
          :file-list="fileList"
          list-type="picture">
          <el-button size="small" type="info">파일업로드</el-button>
          <div slot="tip" :class="['ex-1', {'ex-2':classChk()}]">※ JPG,PNG 파일만 업로드 가능합니다. 해상도는 400 X 600에 최적화 되어있습니다.</div>
        </el-upload>
      </el-form-item>

      <!--
        <ul>
      <li v-for="(item, index) in bodyData" :key="index">
          color : {{item.name}} count : {{item.count}} <button @click="item.count++">수량추가</button>
        <span v-if="item.count === 0">Sold out</span>
        <span v-else>In stock</span>
      </li>
    </ul>
      -->

    </el-form>

    <div class="bottomBtns">
      <el-button v-if="!modifyYn" type="primary" @click="onSubmit('form')">등록</el-button>
      <el-button v-if="modifyYn" type="primary" @click="onUpdate('form')">수정</el-button>
      <el-button type="primary" @click="onCancel">취소</el-button>
      <el-button type="primary" @click="SET_MODAL(true)">미리보기</el-button>
    </div>
    <popup-modal :modalForm="form" :modalFile="fileUrl" v-if="showModal" @close="SET_MODAL(false)"></popup-modal>
  </section>
</template>
<script>
  import PopupModal from '@/views/popup/PopupModal.vue'
  import axios from 'axios'

import { mapState, mapMutations} from 'vuex'

  export default {

    components: {
      PopupModal
      // modal:PopupModal
    },

    // watch: {
    //   'form.subj'(subj, oldSubj) {
    //     console.log('newSubj = ', subj)
    //     console.log('oldSubj = ', oldSubj)
    //   }
    // },
    computed: {
     ...mapState({
       showModal:state => state.modal.showModal
     })
    },
    data() {
      return {
       
        form: {
          subj: '',
          tp: '기본',
          conts: '',
          cnnt_url_yn: '',
          cnnt_url: ''
        },
        
        fileList: [],
        modifyYn: false,
        fileUrl:'',
        imgFile:'',

        tpOptions: [{
          value: '10',
          label: '기본'
        }, {
          value: '20',
          label: '이용동의'
        }, {
          value: '30',
          label: '이벤트'
        }]
      }
    },
    
    methods: {
      ...mapMutations([
        'SET_MODAL'
      ]),
      onSubmit() {
       const formData = new FormData()
       formData.append('form', JSON.stringify(this.form)) //   db에 저장할때 string 안하면 object 로 명시된다.
       if(this.imgFile) formData.append('image', this.imgFile.raw)

       console.log('formData===',formData) // 원래 콘솔로 안나옴..정상


        // 객체가 아닌 formData 그대로 보낸다.
       axios.post(`http://localhost:3000/popup`, formData)
       .then(res => {
         console.log('res.data===', res.data)
       })
       .catch(err => {
         console.log('err===',err)
       })
},
    
      // 글 수정
      onUpdate() {
        
      },

      // 글 등록 취소
      onCancel() {
        
      },

      // 이미지 삭제
      onImgRemove(file, fileList) {
          this.imgFile = ''
      },

      // 이미지 변환
      onImgChange(file, fileList) {
        
        console.log('file = ', file);
        this.fileUrl = file.url
        let fileName = file.name

        let lastDot = fileName.lastIndexOf('.')
        let fileExt = fileName.substring(lastDot).toLowerCase()
        console.log(fileExt)

        if(fileExt !== '.jpg' && fileExt !== '.png' && fileExt !== '.jpeg'){
          alert('jpg png 만 업로드가능합니다.')
          this.fileList = []
        } else {
          this.imgFile = file
        }

      },

      onKeyup(e) {
        // console.log(e);

        // if (this.form.subj.length > 10) {
        //   this.form.subj = this.form.subj.substring(0, 10)
        // }
        
      },
      classChk() {
        return true
      }
    }
    
  }
</script>
<style scoped>
  .ex-1 {
    color: red
  }
  .ex-2 {
    display:none
  }
 
</style>




